@import "../../../styles/utils";
@import '../../../styles/utils/extends';

.selector {
  display: flex;
  flex-direction: column;

  &__title {
    margin-bottom: 8px;
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 15px;
    color: #89939a;
  }

  &__id {
    display: none;
    float: right;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    text-align: right;  
    color: #b4bdc4;

    @include onPhoneAndTablet {
      display: inline-block;
    }
  }

  &__colours {
    margin-bottom: 24px;

    &__text {
      display: flex;
      justify-content: space-between;
    }
  }

  &__colours:after {
    content: "";
    display: block;
    height: 1px;
    background-color: #e2e6e9;
  }

  &__capacity {
    margin-bottom: 32px;
  }

  &__capacity:after {
    content: "";
    display: block;
    height: 1px;
    background-color: #e2e6e9;
  }

  &__price {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 16px;

    &__current {  
      font-weight: 800;
      font-size: 32px;
      line-height: 41px;
      letter-spacing: -0.01em;
  
      color: #313237;
    }
  
    &__old {
      font-weight: 500;
      font-size: 22px;
      line-height: 28px;
      text-decoration-line: line-through;
      
      color: #89939a;
    }
  }

  &__buttons {
    margin-bottom: 32px;
  }

  &__characteristics {
    @extend %flexTechCharacteristics;
    font-size: 12px;
  }
}

.colours {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}

.capacities {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}
